body {
  background: white; }

.homepage-container {
  min-height: 100vh;
  display: grid;
  grid-gap: 0px;
  grid-template-columns: 1fr;
  grid-template-rows: 6fr 1fr;
  grid-template-areas: "background-image-section" "login-register"; }

.background-image-section {
  grid-area: 'background-image-section';
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 4fr;
  background-image: linear-gradient(white, #4E8AFE 80%); }

.title-header {
  display: grid;
  justify-content: center;
  align-content: flex-end; }

.title-header h1 {
  color: #4E8AFE;
  font-size: 3rem;
  letter-spacing: .12em;
  font-weight: 300;
  font-family: 'Raleway', sans-serif;
  margin-bottom: 0px; }

.title-header hr {
  color: white;
  width: 90%;
  border: solid white 1px; }

.title-header span {
  color: white; }

.title-text {
  display: grid;
  grid-template-columns: 8fr 1fr;
  color: white; }

.title-text p {
  margin-top: 50px;
  padding: 20px;
  line-height: 36px;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: .10em; }

.login-register {
  grid-area: login-register;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto;
  justify-content: space-around;
  align-content: center;
  background: white; }

.login-primary {
  padding: 13px;
  background: #FFFFFF;
  border: 2px solid #4E8AFE;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
  width: 160px;
  color: #4E8AFE; }

.register-secondary {
  background: #4E8AFE;
  border-radius: 6px;
  color: white;
  width: 160px;
  padding: 14px; }

@media (min-width: 700px) {
  .title-header, .title-text, .login-register {
    max-width: 900px;
    justify-content: center;
    margin-right: auto;
    margin-left: auto; }
  .login-register {
    grid-gap: 20px; }
  .title-text {
    display: block; }
    .title-text p {
      text-align: center; } }

.login-register-container {
  margin-top: 40%;
  display: grid;
  grid-gap: 10px;
  align-content: center;
  grid-template-columns: 1fr;
  grid-template-areas: "login-register-form-header" "login-register-forms"; }

.login-register-form-header {
  margin: auto 0px;
  padding: 0 20px;
  display: grid;
  grid-template-columns: 1fr; }

.login-register-forms {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 1fr);
  justify-items: center;
  align-items: center;
  grid-gap: 20px; }

.login-register-forms h2 {
  color: #4E8AFE;
  font-size: 3rem;
  letter-spacing: .12em;
  font-weight: 300;
  font-family: 'Raleway', sans-serif;
  margin-bottom: 0px; }

.email-password {
  background-color: red !important;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0 0 0 100px white;
  border: 1px solid black;
  border-radius: 2px;
  color: black;
  width: 90%;
  padding: 14px; }

.email-password input[type="email"] {
  font-family: 'Comfortaa', cursive;
  font-size: 16px;
  line-height: 16px; }

.login-login {
  width: 90%; }

.register-register {
  width: 90%; }

.forgot-password {
  margin-top: 10px;
  color: rgba(0, 0, 0, 0.5);
  justify-self: center;
  font-size: 15px;
  line-height: 17px;
  letter-spacing: 0.04em; }

.login-back {
  color: rgba(0, 0, 0, 0.5);
  font-size: 15px;
  line-height: 17px;
  letter-spacing: 0.04em;
  padding: 0 20px; }

@media (min-width: 700px) {
  .login-register-container {
    margin-top: 10%;
    max-width: 900px;
    justify-content: center;
    margin-right: auto;
    margin-left: auto; } }

